import React from "react";
import { connect } from "react-redux";
import { Sidebar } from "primereact/sidebar";
import localForage from "localforage";
import { Card } from "primereact/card";
import s from "./CustomerDetails.module.sass";
import { InputText } from "primereact/inputtext";
import { RadioButton } from "primereact/radiobutton";

class CustomerDetails extends React.Component {
	state = {
		email: " ",
		Mobile: " ",
		Country: " ",
		State: " ",
		City: " ",
		Shipping: "free"
	};
	hendleKey = key => event => {
		this.setState({ [key]: event.target.value });
	};

	render() {
		return (
			<>
				<Card className={s.Details}>
					<h2>1. Customer Details</h2>
					<div className={s.inputs}>
						<span className="p-float-label">
							<InputText
								name="email"
								id="in"
								value={this.state.email}
								onChange={this.hendleKey("email")}
							/>
							<label htmlFor="in">Email</label>
						</span>
						<span className="p-float-label">
							<InputText
								name="phon"
								id="in"
								value={this.state.Mobile}
								onChange={this.hendleKey("Mobile")}
							/>
							<label htmlFor="in">Mobile</label>
						</span>
						<h4>Shipping To</h4>
						<span className="p-float-label">
							<InputText
								name="country"
								id="in"
								value={this.state.Country}
								onChange={this.hendleKey("Country")}
							/>
							<label htmlFor="in">Country</label>
						</span>
						<span className="p-float-label">
							<InputText
								name="State"
								id="in"
								value={this.state.State}
								onChange={this.hendleKey("State")}
							/>
							<label htmlFor="in">State</label>
						</span>
						<span className="p-float-label">
							<InputText
								name="City"
								id="in"
								value={this.state.City}
								onChange={this.hendleKey("City")}
							/>
							<label htmlFor="in">City</label>
						</span>
					</div>
					<div className={s.radio}>
						<h4>Shipping options</h4>
						<Card className={s.Shipping}>
							<RadioButton
								value="free"
								name="city"
								onChange={this.hendleKey("Shipping")}
								checked={this.state.Shipping === "free"}
							/>
							<div className={s.radioCart}>
								<span>
									Free shipping <i>2-4 days</i>
								</span>
								<span>$0.00</span>
							</div>
						</Card>
						<Card className={s.Shipping}>
							<RadioButton
								value="local"
								name="city"
								onChange={this.hendleKey("Shipping")}
								checked={this.state.Shipping === "local"}
							/>
							<div className={s.radioCart}>
								<span>
									Local Pickup{" "}
									<i>Store A and Store B open 24h/7d</i>
								</span>
								<span>$0.00</span>
							</div>
						</Card>
						<Card className={s.Shipping}>
							<RadioButton
								value="express"
								name="city"
								onChange={this.hendleKey("Shipping")}
								checked={this.state.Shipping === "express"}
							/>
							<div className={s.radioCart}>
								<span>
									DHL Express <i>1 day</i>
								</span>
								<span>$60.00</span>
							</div>
						</Card>
					</div>

					{this.state.Shipping === "free" ? (
						<div>
							<h4>Payment options</h4>
							<Card className={s.Shipping}>
								<RadioButton
									value="PayPal"
									name="city"
									onChange={this.hendleKey("Shipping")}
									checked={this.state.Shipping === "PayPal"}
								/>
								<div className={s.radioCart}>
									<span>PayPal</span>
								</div>
							</Card>
						</div>
					) : this.state.Shipping === "local" ? (
						<div>
							<h4>Payment options</h4>
							<Card className={s.Shipping}>
								<RadioButton
									value="Cash"
									name="city"
									onChange={this.hendleKey("Shipping")}
									checked={this.state.Shipping === "Cash"}
								/>
								<div className={s.radioCart}>
									<span>Cash</span>
								</div>
							</Card>
						</div>
					) : (
						<div> </div>
					)}
				</Card>
			</>
		);
	}
}

const a = state => ({
	userDate: state.user
});
const b = dispatch => {
	return {
		updateUserDate: payload => dispatch({ type: "UPDATE_USER", payload })
	};
};

export default connect(
	a,
	b
)(CustomerDetails);
