import config from "../config";

const { API_HOST, API_VERSION } = config;
const token = "dddddddddddddddddddd";

export default class API {
	constructor(type = "users") {
		this.type = type;
		this.token = token;
		this.URL = `${API_HOST}${API_VERSION ? "/" : ""}${API_VERSION}/${type}`;
	}
	get(path = "", isAuts = false) {
		const headers = {};
		if (isAuts) headers.authorization = `Bearer ${isAuts}`;
		return fetch(`${this.URL}/${path}`, {
			method: "GET",
			headers
		}).then(res => res.json());
	}
	delet(path = "", isAuts = false) {
		const headers = {};
		if (isAuts) headers.authorization = `Bearer ${this.token}`;
		return fetch(`${this.URL}/${path}`, {
			method: "DELETE"
		}).then(res => res.json());
	}
	post(path = "", isAuts = false, body = {}) {
		const headers = {};
		if (isAuts) headers.authorization = `Bearer ${this.token}`;
		if (body) headers["content-type"] = "application/json";
		return fetch(`${this.URL}/${path}`, {
			method: "POST",
			body: JSON.stringify(body),
			headers
		}).then(res => res.json());
	}
	put(path = "", isAuts = false, body = {}) {
		const headers = {};
		if (isAuts) headers.authorization = `Bearer ${this.token}`;
		if (body) headers["content-type"] = "application/json";
		return fetch(`${this.URL}/${path}`, {
			method: "PUT",
			body: JSON.stringify(body),
			headers
		}).then(res => res.json());
	}
}
