import React from "react";
import { connect } from "react-redux";
import { Sidebar } from "primereact/sidebar";
import localForage from "localforage";
import { Card } from "primereact/card";
import s from "./OrderSummary.module.sass";
import { Button } from "primereact/button";

const header = src => <img style={{ width: "200px" }} alt="Card" src={src} />;
const footer = ({ removeGood, good }) => (
	<span>
		<Button
			label="Отменить"
			icon="pi pi-times"
			className="p-button-secondary"
			onClick={() => removeGood(good.id)}
		/>
	</span>
);

class OrderSummary extends React.Component {
	state = {};

	render() {
		const { cartItems, removeGood } = this.props;
		return (
			<>
				<Card>
					<h2>Order Summary</h2>
					<hr />
					<div className={s.Gods}>
						<ul className={s.List}>
							{cartItems.length === 0 ? (
								<li>Выберите товар</li>
							) : (
								cartItems.map((item, key) => {
									return (
										<li key={key}>
											<Card
												footer={footer({
													removeGood,
													good: item
												})}
												header={header(item.image)}
											>
												{item.name} {item.price} $
											</Card>
										</li>
									);
								})
							)}
						</ul>
					</div>
					<div className={s.Check}>
						<div>
							<b>
								Промежуточный итог:
								{cartItems.length === 0
									? 0
									: cartItems
											.map(x => Number(x.price))
											.reduce((a, b) => a + b)}{" "}
								$
							</b>
						</div>
						<span>Перевозка</span>
						<hr />
						<b>
							Всего к оплате:
							{cartItems.length === 0
								? 0
								: cartItems
										.map(x => Number(x.price))
										.reduce((a, b) => a + b)}{" "}
							$
						</b>
					</div>
				</Card>
			</>
		);
	}
}

const a = state => ({
	cartItems: state.cart.preparedGood
});
const b = dispatch => {
	return {
		removeGood: _id => dispatch({ type: "REMOVE_GOOD", payload: _id })
	};
};

export default connect(
	a,
	b
)(OrderSummary);
