import React from "react";
import { connect } from "react-redux";
import s from "./Menu.module.sass";

class MenuPanel extends React.Component {
	state = {};

	render() {
		const {Link, activeCategories, handleMemuShow, showMenu} = this.props;
		return <>
		<div
			onMouseOver={() => {
				handleMemuShow(true);
			}}
			onMouseLeave={() => {
			handleMemuShow(false);
			}}
			style={{ display: showMenu ? "block" : "none" }}
			className={s.FullMenu}
			> 
				<ul className={s.Categories}> 
				{Object.keys(
					activeCategories
					).map((cat, i) => {
					return (<li className={s.Topics} key={i}><span>{cat}</span>
						<ul className={s.Sub}>
						{ activeCategories[cat].map((sub, key) => {
							return ( <li  key={key}>{
								sub.title ? <Link to={sub.link}>{sub.title}</Link> : sub
							}</li> )
						})

						}
						</ul>

						</li>)
				})
				}
				</ul>
				</div>
		</>


	}


}




export default MenuPanel;