import React from "react";
import s from "./Profile.module.sass";
import { Link } from "react-router-dom";
import localForage from "localforage";
import { connect } from "react-redux";
import UsersService from "../../services/Users";

class Profile extends React.PureComponent {
	state = {
		element: 0
	};
	async componentDidMount() {
		const { userData, updateUserDate } = this.props;

		console.log(userData);

		if (userData.token && !userData.createdAt) {
			const userResult = await UsersService.get("me", userData.token);
			updateUserDate({
				email: userResult.email,
				createdAt: userResult.createdAt,
				userName: userResult.userName,
				name: userResult.name,
				phone: userResult.phone,
				dateOfBirth: userResult.dateOfBirth,
				city: userResult.city,
				address: userResult.address
			});
		}
	}

	hendleExit = () => {
		const { updateUserDate } = this.props;

		localForage.setItem("userDate", null);
		updateUserDate(null);
	};

	render() {
		const { Link, t = () => {}, userList, userData } = this.props;
		console.log(userData, "data");

		return (
			<>
				<div className={s.Profile}>
					<div className={s.ProfileData}>
						<h2>{t("Personal_data")}</h2>
						<br />
						<div>
							<span> {t("Name")}:</span>
							<span> {userList.name}</span>
						</div>
						<div>
							<span> {t("Email")}: </span>
							<span>{userList.email}</span>
						</div>
						<div>
							<span> {t("Phone")}: </span>
							<span> {userList.phone}</span>
						</div>
						<div>
							<span>{t("Date_of_Birth")}:</span>
							<span> {userList.dateOfBirth} </span>
						</div>
						<div>
							<span> {t("City")}:</span>
							<span> {userList.city} </span>
						</div>
						<div>
							<span> {t("Address")}:</span>
							<span> {userList.address} </span>
						</div>
					</div>
					<Link to="/">
						<button onClick={this.hendleExit}>Выход</button>
					</Link>

					<div className={s.ProfilePhoto}></div>
				</div>
			</>
		);
	}
}

const a = store => ({
	userData: store.user,
	t: store.translation.t,
	userList: store.userList
});
const b = dispatch => {
	return {
		updateUserDate: payload => dispatch({ type: "UPDATE_USER", payload })
	};
};

export default connect(
	a,
	b
)(Profile);
