import React from "react";
import { connect } from "react-redux";
import s from "./Header.module.sass";
// import {Button} from "primereact/button";
import { InputText } from "primereact/inputtext";
import Menu from "../Menu/";
import { Dropdown } from "primereact/dropdown";
import localForage from "localforage";
import logo2 from "../../image/logo2.png";
import shopping from "../../image/shopping-bag.svg";

const imagesLinnks = {
	logo: logo2,
	burger: "https://static.thenounproject.com/png/204478-200.png",
	cart: shopping,
	login: "http://cdn.onlinewebfonts.com/svg/img_276773.png"
};

const languagesSelectItems = [
	{ label: "en", value: "en" },
	{ label: "ru", value: "ru" }
];

class Header extends React.Component {
	state = {};

	async componentWillMount() {
		const lang = await localForage.getItem("language");

		this.setLanguage(lang || "en");
	}

	setLanguage = language => {
		const { chengeLanguage } = this.props;
		localForage.setItem("language", language);
		chengeLanguage(language);
	};

	render() {
		const {
			cartItems = [],
			Link,
			chengeCart,
			userDate,
			language,
			t
		} = this.props;

		return (
			<>
				<header className={s.header}>
					<div className={s.wrapper}>
						<Dropdown
							className={s.Languages}
							value={this.state.languages}
							options={languagesSelectItems}
							onChange={event => {
								this.setLanguage(event.value);
								this.setState({ languages: event.value });
							}}
							placeholder={language}
						/>
						<div className="p-inputgroup">
							<span className="p-inputgroup-addon">
								<i className="pi pi-search"></i>
							</span>
							<InputText placeholder={t("header_search")} />
						</div>
						<div className={s.logo}>
							<Link to="/">
								<img src={imagesLinnks.logo} />
							</Link>
						</div>

						{/*<div
							className={s.burgerIcon}
							onClick={this.props.chengeMenu}
						>
							<img src={imagesLinnks.burger} />
						</div>*/}
						<div onClick={() => chengeCart()} className={s.Cart}>
							<img src={imagesLinnks.cart} width="50px" />{" "}
							<span>{cartItems.length}</span>
						</div>
						<div>
							<Link to={userDate.token ? "/profile" : "/login"}>
								<img src={imagesLinnks.login} width="50px" />{" "}
							</Link>
						</div>
					</div>
					<Menu Link={this.props.Link} />
				</header>
			</>
		);
	}
}

const a = store => ({
	cartItems: store.cart.preparedGood,
	userDate: store.user,
	language: store.translation.language,
	t: store.translation.t
});

const b = dispatch => {
	return {
		chengeMenu: () => dispatch({ type: "CHENGE_MENU_TYPE" }),
		chengeCart: () => dispatch({ type: "CHENGE_CART_TYPE" }),
		chengeLanguage: language =>
			dispatch({ type: "CHENGE_LANGUAGE", payload: language })
	};
};

export default connect(
	a,
	b
)(Header);
