import React from "react";
import Header from "../components/Header/";
import BurgerMenu from "../components/BurgerMenu/";
import Goods from "../components/Goods/";
import Footer from "../components/Footer/";
import Login from "./Login";
import Main from "./Main";
import Register from "./Register";
import Checkout from "./Checkout";
import { connect } from "react-redux";
import localForage from "localforage";
import Cart from "../components/Cart/";
import GoodInfo from "./GoodInfo.jsx";
import Profile from "../components/Profile/";
import Filter from "./Filter";
import GoodsServices from "../services/Goods";
import UsersServices from "../services/Users";

import {
  Route,
  Link,
  Switch,
  BrowserRouter,
  withRouter
} from "react-router-dom";
import { Sidebar } from "primereact/sidebar";
import Slider from "../components/Slider/";

const Page2 = ({ Link }) => (
  <div>
    Page2
    <Link to="/">
      <button>CO to 1</button>
    </Link>
  </div>
);
const Page404 = ({ Link }) => <div>404</div>;

class Routing extends React.Component {
  state = {};
  async componentWillMount() {
    const { userList } = this.props;
    const { userDate, updateUserDate, addGood, setGoods } = this.props;
    console.log(GoodsServices);
    const goodlist = await GoodsServices.get();
    console.log(goodlist, "8888");
    setGoods(goodlist);

    if (!userDate.token) {
      const savedUserDate = await localForage.getItem("userData");
      if (savedUserDate) {
        updateUserDate(savedUserDate);
      }
      const Goods = (await localForage.getItem("Goods")) || [];
      Goods.forEach(good => addGood(good));
    }
  }

  render() {
    const { history } = this.props;
    return (
      <BrowserRouter>
        <main history={history}>
          <Header Link={Link} />
          <div style={{ width: "100%", height: "150px" }}></div>
          <BurgerMenu />
          <Cart Link={Link} />

          <Switch>
            <Route exact path="/" component={() => <Main Link={Link} />} />
            <Route
              exact
              path="/good/:id"
              component={() => <GoodInfo Link={Link} />}
            />
            <Route
              exact
              path="/page2"
              component={() => <Page2 Link={Link} />}
            />
            <Route
              exact
              path="/login"
              component={() => <Login Link={Link} />}
            />
            <Route
              exact
              path="/register"
              component={() => <Register Link={Link} />}
            />

            <Route
              exact
              path="/checkout"
              component={() => <Checkout Link={Link} />}
            />

            <Route
              exact
              path="/profile"
              component={() => <Profile Link={Link} />}
            />
            <Route
              exact
              path="/filter/:category"
              component={() => <Filter Link={Link} />}
            />

            <Route exact path="*" component={() => <Page404 Link={Link} />} />
          </Switch>
          <Footer />
        </main>
      </BrowserRouter>
    );
  }
}
const a = store => ({
  userDate: store.user,
  userList: store.userList
});
const b = dispatch => {
  return {
    updateUserDate: payload => dispatch({ type: "UPDATE_USER", payload }),
    addGood: _good => dispatch({ type: "ADD_GOOD", payload: _good }),
    setGoods: _good => dispatch({ type: "SET_GOODS", payload: _good }),
    userList: _userList =>
      dispatch({ type: "UPDATE_USER", userList: _userList })
  };
};

export default connect(
  a,
  b
)(Routing);
