const Store = t => {
	let Store = t("Footer_Store");

	return Store;
};
const Contacts = t => {
	let Contacts = [
		t("Footer_Store_104_N_Stagecoach_Rd"),
		t("Footer_Store_(207)_564-8482"),
		t("Footer_Store_Dover_Foxcroft,_ME,_04426"),
		t("Footer_Store_sales@shop.com")
	];

	return Contacts;
};

const siteSections = t => {
	let siteSections = [
		{
			title: t("Footer_siteSections_Company"),
			categories: [
				{ name: t("Footer_siteSections_Company_About"), link: "" },
				{ name: t("Footer_siteSections_Company_Blog"), link: "" },
				{
					name: t("Footer_siteSections_Company_Terms_of_Service"),
					link: ""
				},
				{
					name: t("Footer_siteSections_Company_Privacy_Policy"),
					link: ""
				}
			]
		},
		{
			title: t("Footer_siteSections_Customer_service"),
			categories: [
				{
					name: t("Footer_siteSections_Customer_Shipping_&_returns"),
					link: ""
				},
				{
					name: t("Footer_siteSections_Customer_Conditions_of_Use"),
					link: ""
				},
				{ name: t("Footer_siteSections_Customer_Sitemap"), link: "" }
			]
		}
	];
	console.log(siteSections);
	return siteSections;
};

class Footer extends React.Component {
	state = {
		element: 0,
		FooterElementsNew: [{ title: "", categories: {} }]
	};

	/*componentWillMount() {
		this.setState({
			FooterElementsNew: FooterElements
		});
	}*/

	render() {
		const activeFooter = this.state.element;
		const { t } = this.props;
		return (
			<>
				<footer className={s.Footer}>
					<div className={s.Logo}>
						<img src={footerImagesLinnks.logo} />
						<div className={s.Store}>
							{" "}
							<span>{Store(t)}</span>{" "}
						</div>
						<ul className={s.Contacts}>
							{Object.keys(Contacts(t)).map((elem, index) => {
								return <li key={index}>{Contacts(t)[elem]}</li>;
							})}
						</ul>
					</div>
					<div>
						<ul className={s.menuFooter}>
							{siteSections(t).map((cat, i) => {
								console.log(siteSections(t));
								return (
									<li key={i}>
										<span>{cat.title}</span>
										<ul className={s.Sub}>
											{Object.values(
												siteSections(t)[i]
											).map((sub, key) => {
												console.log(siteSections(t));
												return typeof sub === "string"
													? ""
													: (sub || []).map(_sub => (
															<li key={key}>
																<Link
																	to={
																		_sub.link
																	}
																>
																	{_sub.name}
																</Link>
															</li>
													  ));
											})}
										</ul>
									</li>
								);
							})}
						</ul>
					</div>
				</footer>
			</>
		);
	}
}

const a = store => ({
	language: store.translation.language,
	t: store.translation.t
});

const b = dispatch => {
	return {
		chengeLanguage: language =>
			dispatch({ type: "CHENGE_LANGUAGE", payload: language })
	};
};

export default connect(
	a,
	b
)(Footer);
