import React from "react";
import { connect } from "react-redux";
import localForage from "localforage";
import CustomerDetails from "../components/CustomerDetails/";
import OrderSummary from "../components/OrderSummary/";
import BreadCrumb from "../components/BreadCrumb/";
import GoodPhoto from "../components/GoodPhoto/";
import GoodCounter from "../components/GoodCounter/";
import GoodDescription from "../components/GoodDescription/";

const GoodStyles = {
	display: "flex",
	flexDirection: "column",
	justifyContent: "space-around"
};
const LeftStule = {
	display: "flex",
	flexDirection: "row",
	justifyContent: "space-around"
};

class GoodInfo extends React.Component {
	state = {};

	render() {
		const { goodsList = [] } = this.props;
		const goodId = window.location.pathname.replace("/good/", "");
		const findetGood = goodsList.find(_good => _good.id === goodId);
		return (
			<>
				{!findetGood ? (
					"Товар не найден"
				) : (
					<div style={GoodStyles}>
						<div style={LeftStule}>
							<div>
								<BreadCrumb category={findetGood.category} />
								<GoodPhoto photo={findetGood.image} />
							</div>
							<GoodCounter
								good={findetGood}
								name={findetGood.name}
								price={findetGood.price}
							/>
						</div>
						<GoodDescription
							description={findetGood.description}
							characteristics={findetGood.characteristics}
						/>
						{/*
						1. menu
						2. photo
						3. счетчик и покупка
						4. описание и характеристики */}
					</div>
				)}
			</>
		);
	}
}

const a = state => ({
	userDate: state.user,
	goodsList: state.goods.goodsList
});
const b = dispatch => {
	return {
		updateUserDate: payload => dispatch({ type: "UPDATE_USER", payload }),
		addGood: _good => dispatch({ type: "ADD_GOOD", payload: _good }),
		removeGood: _id => dispatch({ type: "REMOVE_GOOD", payload: _id })
	};
};

export default connect(
	a,
	b
)(GoodInfo);
