import React from "react";
import { connect } from "react-redux";
import s from "./BurgerMenu.module.sass";
import { Sidebar } from "primereact/sidebar";

class BurgerMenu extends React.Component {
	state = {};

	render() {
		console.log(this.props);
		return (
			<div className={s.BurgerMenu}>
				<Sidebar
					visible={this.props.showMenu}
					onHide={this.props.chengeMenu}
				>
					<ul>
						<li>Домой</li>
						<li>О сайте </li>
						<li>Контакты</li>
					</ul>
				</Sidebar>
			</div>
		);
	}
}

const a = state => ({
	showMenu: state.burgerMenu.showMenu
});
const b = dispatch => {
	return {
		chengeMenu: () => dispatch({ type: "CHENGE_MENU_TYPE" })
	};
};

export default connect(
	a,
	b
)(BurgerMenu);

