import React from "react";
import { Link } from "react-router-dom";
import s from "./GoodDescription.module.sass";

class GoodDescription extends React.Component {
	state = {};

	render() {
		const { description = "", characteristics = "" } = this.props;

		return (
			<div className={s.GoodDescription}>
				<div className={s.Description}> {description}</div>
				<div className={s.Characteristics}>{characteristics} </div>
			</div>
		);
	}
}

export default GoodDescription;
