const initialState = {
	id: 0,
	userName: "zhaivorondima@gmail.com",
	name: "Жайворон Дмитрий",
	email: "zhaivorondima@gmail.com",
	phone: "+380 (95) 617-74-37",
	dateOfBirth: "22.01.1994",
	city: "Полтава",
	address: "Адрес"
};

function userReducer(state = initialState, action) {
	switch (action.type) {
		case "UPDATE_USER":
			return action.payload ? { ...state, ...action.payload } : {};
		default:
			return state;
	}
}

export default userReducer;
