import React from "react";
import { connect } from "react-redux";
import s from "./Cart.module.sass";
import { Sidebar } from "primereact/sidebar";
import { Card } from "primereact/card";
import { Button } from "primereact/button";

const header = src => <img alt="Card" src={src} />;
const footer = ({ removeGood, good }) => (
	<span>
		<Button
			label="Отменить"
			icon="pi pi-times"
			className="p-button-secondary"
			onClick={() => removeGood(good.id)}
		/>
	</span>
);

class Cart extends React.Component {
	state = {};

	render() {
		const { cartItems = [], removeGood, Link } = this.props;

		return (
			<div className={s.Cart}>
				<Sidebar
					position="right"
					visible={this.props.showCart}
					onHide={this.props.chengeCart}
				>
					<ul className={s.List}>
						{cartItems.length === 0 ? (
							<li>Выберите товар</li>
						) : (
							cartItems.map((item, key) => {
								return (
									<li key={key}>
										<Card
											footer={footer({
												removeGood,
												good: item
											})}
											header={header(item.image)}
										>
											{item.name} {item.price} $
										</Card>
									</li>
								);
							})
						)}
					</ul>
					<b>
						Всего к оплате:
						{cartItems.length === 0
							? 0
							: cartItems
									.map(x => Number(x.price))
									.reduce((a, b) => a + b)}{" "}
						$
					</b>
					<Link to="/checkout">
						<Button
							onClick={this.props.chengeCart}
							className={` p-button-success ${s.Checkout}`}
							label="Оплатить"
							icon="pi pi-check"
						/>
					</Link>
				</Sidebar>
			</div>
		);
	}
}

const a = state => ({
	showCart: state.cart.showCart,
	cartItems: state.cart.preparedGood
});
const b = dispatch => {
	return {
		chengeCart: () => dispatch({ type: "CHENGE_CART_TYPE" }),
		removeGood: _id => dispatch({ type: "REMOVE_GOOD", payload: _id })
	};
};

export default connect(
	a,
	b
)(Cart);

