import React from "react";
import { Link } from "react-router-dom";
import { Lightbox } from "primereact/lightbox";
import s from "./GoodPhoto.module.sass";

class GoodPhoto extends React.Component {
	state = {};

	render() {
		const { photo } = this.props;

		return (
			<div className={s.GoodPhoto}>
				<Lightbox
					type="images"
					images={[
						{
							source: photo,
							thumbnail: photo,
							title: "Goods Preview"
						}
					]}
				/>
			</div>
		);
	}
}

export default GoodPhoto;
