import React from "react";
import Slider from "react-slick";
import s from "./Slider.module.sass";

const slides = [
  "https://cdn1.mygazeta.com/i/2014/02/internet_magazin.jpg",
  "https://viduli.com.ua/userfiles/bs/dizayn-bez-nazvaniya-3_11.png",
  "https://scontent.fiev10-1.fna.fbcdn.net/v/t1.0-9/17200970_1849092872033259_6980341639607640545_n.jpg?_nc_cat=108&_nc_oc=AQlNeKBXWDxIj_CKGPFz_f1pfrHW-UG_NIfWfQUKGBiRVLSvpFrNL-2R0SFaEGdj3Rw&_nc_ht=scontent.fiev10-1.fna&oh=cfb58c09c91569d878252e2c1cc7bb75&oe=5DC95B65"
];
class SimplSlider extends React.Component {
  state = {};

  render() {
    var settings = {
      dots: true,
      infinite: true,
      arrows: true,
      autoplay: true,
      speed: 1000,
      slidesToShow: 1,
      slidesToScroll: 1
    };
    return (
      <Slider className={s.Slider} {...settings}>
        <div>
          <img src={slides[0]} />
        </div>
        <div>
          <img src={slides[1]} />
        </div>
        <div>
          <img src={slides[2]} />
        </div>
      </Slider>
    );
  }
}

export default SimplSlider;
