import React from "react";
import { connect } from "react-redux";
import s from "./Menu.module.sass";
import MenuPanel from "./MenuPanel";

const MenuLanguage = t => {
	return [
		{
			title: t("Menu_CLOTHING"),
			alias: "CLOTHING",
			categories: {
				[t("man")]: [{ title: t("Menu_Page2"), link: "/page2" }],
				[t("woman")]: [],
				[t("boys")]: [],
				[t("girls")]: []
			}
		},
		{
			title: t("Menu_FOOTWEAR"),
			alias: "FOOTWEAR",
			categories: {
				[t("man")]: [
					t("Menu_FOOTWEAR_Trainers"),
					t("Menu_FOOTWEAR_Tennis_Shoes"),
					t("Menu_FOOTWEAR_Skate_Shoes"),
					t("Menu_FOOTWEAR_Shoes"),
					t("Menu_FOOTWEAR_Sandals_and_Flip_Flops"),
					t("Menu_FOOTWEAR_Hi_Tops"),
					t("Menu_FOOTWEAR_Football_Boots"),
					t("Menu_FOOTWEAR_Canvas_Shoes")
				],
				[t("woman")]: [
					t("Menu_FOOTWEAR_Canvas_Shoes"),
					t("Menu_FOOTWEAR_Football_Boots"),
					t("Menu_FOOTWEAR_Hi_Tops"),
					t("Menu_FOOTWEAR_Sandals_and_Flip_Flops"),
					t("Menu_FOOTWEAR_Shoes")
				],
				[t("boys")]: [],
				[t("girls")]: []
			}
		},
		{
			title: t("Menu_DRONES"),
			alias: "DRONES",
			categories: {
				BRANDS: [
					t("Menu_DRONES_Parrot"),
					t("Menu_DRONES_DJI"),
					t("Menu_DRONES_Yuneec"),
					t("Menu_DRONES_Holy_Stone")
				],
				ACCESSORIES: [
					t("Menu_DRONES_Batteries"),
					t("Menu_DRONES_Battery_Chargers"),
					t("Menu_DRONES_Cameras"),
					t("Menu_DRONES_Camera_Gimbals"),
					t("Menu_DRONES_Drone_Cases")
				]
			}
		},
		{ title: t("Menu_BOOKS"), alias: "BOOKS", categories: {} },
		{ title: t("Menu_ELECTRONICS"), alias: "ELECTRONICS", categories: {} },
		{ title: t("Menu_TESTE"), alias: "TESTE", categories: {} }
	];
};

class Menu extends React.Component {
	state = {
		showMenu: false,
		active: 0,
		MenuElementsNew: [{ title: "", categories: {} }]
	};
	handleMemuShow = type => {
		this.setState({ showMenu: type });
	};

	componentWillMount() {
		this.setState({
			MenuElementsNew: MenuLanguage
		});
	}

	render() {
		const { Link, t, chengeCategori } = this.props;
		console.log(this.state.MenuElementsNew(t));
		const activeCategories = this.state.MenuElementsNew(t)[
			this.state.active
		].categories;
		return (
			<div className={s.Menu}>
				<ul className={s.Wrapper}>
					{this.state.MenuElementsNew(t).map((elem, index) => {
						return (
							<li
								style={{
									background:
										this.state.showMenu &&
										this.state.active === index
											? "#f5f5f5"
											: "#fff"
								}}
								onMouseOver={() => {
									this.setState({
										showMenu: true,
										active: index
									});
								}}
								onMouseLeave={() => {
									this.setState({ showMenu: false });
								}}
								key={index}
							>
								<Link
									to={`/filter/${(
										elem.alias || ""
									).toLowerCase()}`}
									onClick={() =>
										chengeCategori(
											(elem.alias || "").toLowerCase()
										)
									}
								>
									{" "}
									{elem.title}{" "}
								</Link>
							</li>
						);
					})}
				</ul>
				<MenuPanel
					activeCategories={activeCategories}
					Link={Link}
					showMenu={this.state.showMenu}
					handleMemuShow={this.handleMemuShow}
				/>
			</div>
		);
	}
}

const a = store => ({
	language: store.translation.language,
	t: store.translation.t
});
const b = dispatch => {
	return {
		chengeMenu: () => dispatch({ type: "CHENGE_MENU_TYPE" }),
		chengeCategori: category =>
			dispatch({ type: "CHANGE_CATEGORI", payload: category }),
		hengeLanguage: language =>
			dispatch({ type: "CHENGE_LANGUAGE", payload: language })
	};
};

export default connect(
	a,
	b
)(Menu);

