import React from "react";
import { Link } from "react-router-dom";
import s from "./GoodCounter.module.sass";
import { connect } from "react-redux";
import { Button } from "primereact/button";
import { Dropdown } from "primereact/dropdown";
import localForage from "localforage";

class GoodCounter extends React.Component {
	state = {
		count: 1
	};

	handleClick = type => () => {
		if (type === "plus") {
			this.setState({ count: this.state.count + 1 });
		} else {
			this.setState({
				count: this.state.count !== 1 ? this.state.count - 1 : 1
			});
		}
	};
	handleAddToCard = () => {
		const { addGood, good } = this.props;
		const { count } = this.state;
		for (let i = 0; i < count; i++) {
			addGood(good);
		}
	};
	render() {
		const { name = "", price = 0, addGood, t } = this.props;
		const { count = 0 } = this.state;

		return (
			<div className={s.GoodCounter}>
				<h2 className={s.Name}>{name}</h2>
				<div className={s.Price}>
					<h2>$ {price}</h2>
				</div>
				<div className={s.Count}>
					<button onClick={this.handleClick("minus")}> - </button>
					<span> {count} </span>
					<button onClick={this.handleClick("plus")}> + </button>
				</div>
				<div className={s.AddToCart}>
					<Button
						label={t("Add_to_Cart")}
						className="p-button-success"
						onClick={this.handleAddToCard}
					/>
				</div>
			</div>
		);
	}
}

const a = store => ({
	language: store.translation.language,
	t: store.translation.t
});
const b = dispatch => {
	return {
		addGood: _good => dispatch({ type: "ADD_GOOD", payload: _good }),
		chengeLanguage: language =>
			dispatch({ type: "CHENGE_LANGUAGE", payload: language })
	};
};

export default connect(
	a,
	b
)(GoodCounter);
