import React from "react";
import s from "./Login.module.sass";
import { connect } from "react-redux";
import { Sidebar } from "primereact/sidebar";
import localForage from "localforage";
import { Redirect } from "react-router-dom";
import AuthServises from "../services/Auth";

class Register extends React.Component {
	state = {
		login: "eve.holt@reqres.in",
		password: "pistol"
	};
	handleChangeByKey = key => _value => {
		if (_value.target) _value = _value.target.value;

		this.setState({ [key]: _value });
	};

	sendUserData = async () => {
		try {
			const { updateUserDate } = this.props;
			const { login, password } = this.state;
			const userData = await AuthServises.post("local/register", false, {
				password,
				username: login,
				email: login
			});
			if (!userData.jwt) throw new Error(userData.message);
			userData.token = userData.jwt;
			localForage.setItem("userData", userData);
			updateUserDate(userData);
		} catch (error) {
			alert(error.message);
		}
		/*fetch("https://reqres.in/api/register", options).then(response =>
			response.json().then(body => {
				if (!body.error) {
					this.setState({ ...body });
					const userNewDate = { ...this.state, ...body };
					delete userNewDate.password;

					

					
				} else {
					alert(body.error);
				}
			})
		);*/
	};

	render() {
		const { userDate } = this.props;
		const { login, password } = this.state;
		return userDate.token ? (
			<Redirect to="/profile" />
		) : (
			<>
				<div className={s.auch}>
					Login:{" "}
					<input
						onChange={this.handleChangeByKey("login")}
						value={login}
					/>{" "}
					{login}
					<br />
					Password:{" "}
					<input
						content-type="password"
						onChange={this.handleChangeByKey("password")}
						value={password}
					/>{" "}
					{password}
					<br />
					<button onClick={this.sendUserData}>
						Зарегистрироватся
					</button>
				</div>
			</>
		);
	}
}

const a = store => ({
	userDate: store.user
});
const b = dispatch => {
	return {
		updateUserDate: payload => dispatch({ type: "UPDATE_USER", payload })
	};
};

export default connect(
	a,
	b
)(Register);
