import React from "react";
import { connect } from "react-redux";
import localForage from "localforage";
import s from "./Goods.module.sass";
import { Card } from "primereact/card";
import { Button } from "primereact/button";
import { Link } from "react-router-dom";

const footer = ({ good, addGood, removeGood }) => {
  const saveGood = async () => {
    const Goods = (await localForage.getItem("Goods")) || [];
    const newGood = [...Goods, good];
    localForage.setItem("Goods", newGood);
  };
  const deleteGood = async () => {
    const Goods = (await localForage.getItem("Goods")) || [];
    const newGood = Goods.filter(_good => _good.id !== good.id);
    localForage.setItem("Goods", newGood);
  };
  return (
    <span>
      <Button
        label="Купить"
        icon="pi pi-check"
        style={{ marginRight: ".25em" }}
        onClick={() => {
          addGood(good);
          saveGood();
        }}
      />
      <Button
        label="Убрать с корзины"
        icon="pi pi-times"
        className="p-button-secondary"
        onClick={() => {
          removeGood(good.id);
          deleteGood();
        }}
      />
    </span>
  );
};

class Goods extends React.Component {
  state = {};
  componentDidMount() {
    const { clearFilter, clierFilters } = this.props;
    if (clearFilter) {
      return clierFilters();
    }
  }
  render() {
    const {
      goodsList,
      addGood,
      removeGood,
      cartItems,
      history,
      brand,
      size,
      year,
      rangeValues,
      clearFilter
    } = this.props;
    const category = window.location.pathname.split("/")[2];
    return (
      <div className={s.GoodsGrid}>
        {goodsList
          .filter(good => {
            if (
              brand.length === 0 &&
              size.length === 0 &&
              year.length === 0 &&
              rangeValues[0] === 400 &&
              rangeValues[1] === 9999
            ) {
              return true;
            }
            const ckeckBrand = brand.includes(good.brand) || brand.length === 0;
            const ckeckSize =
              good.size.find(_size => {
                return size.includes(_size);
              }) || size.length === 0;
            const ckeckYear = year.includes(good.year) || year.length === 0;
            const ckeckRangeValues =
              good.price >= rangeValues[0] && good.price <= rangeValues[1];

            return ckeckBrand && ckeckSize && ckeckYear && ckeckRangeValues;
          })
          .filter(good =>
            // если передана category и она совпадает с категорией товара то показать товар
            category && category === good.category
              ? good
              : // если категория не передана показываем товар
              !category
              ? good
              : // если
                false
          )
          .map((good, key) => {
            const header = (
              <Link to={`/good/${good.id}`}>
                <img className={s.goodimg} alt="Card" src={good.image} />
              </Link>
            );
            return (
              <Card
                className={s.pCard}
                key={key}
                footer={footer({ good, addGood, removeGood })}
                header={header}
              >
                {good.name}
                <i> {good.price}$ </i>
                <span className={s.buyCount}>
                  {cartItems.filter(x => x.id === good.id).length || ""}
                </span>
              </Card>
            );
          })}
      </div>
    );
  }
}

const a = store => ({
  goodsList: store.goods.goodsList,
  cartItems: store.cart.preparedGood,
  brand: store.filter.brand,
  size: store.filter.size,
  year: store.filter.year,
  rangeValues: store.filter.rangeValues,
  category: store.filter.category
});
const b = dispatch => {
  return {
    addGood: _good => dispatch({ type: "ADD_GOOD", payload: _good }),
    removeGood: _id => dispatch({ type: "REMOVE_GOOD", payload: _id }),
    filterByKey: payload => dispatch({ type: "FILTER_BY_KEY", payload }),
    clierFilters: () => dispatch({ type: "CLIER_FILTER" })
  };
};

export default connect(
  a,
  b
)(Goods);
