import React from "react";
import s from "./BreadCrumb.module.sass";
import { Link } from "react-router-dom";
import { connect } from "react-redux";

class BreadCrumb extends React.Component {
	state = {};

	render() {
		const { category = "", t = () => {} } = this.props;

		return (
			<>
				<ul className={s.BreadCrumb}>
					<li>
						<Link to="/"> {t("Home")}</Link>
					</li>
					<li>/</li>
					<li>
						<Link to=""> {t("Category")}</Link>{" "}
					</li>
					<li>/</li>
					<li className={s.Category}>
						{" "}
						<Link to={`/filter/${category}`}> {category}</Link>
					</li>
				</ul>
			</>
		);
	}
}
const a = store => ({
	language: store.translation.language,
	t: store.translation.t
});

const b = dispatch => {
	return {
		chengeLanguage: language =>
			dispatch({ type: "CHENGE_LANGUAGE", payload: language })
	};
};

export default connect(
	a,
	b
)(BreadCrumb);
