const initialState = {
	showCart: false,
	preparedGood: []
};

function cartReducer(state = initialState, action) {
	switch (action.type) {
		case "CHENGE_CART_TYPE":
			return { ...state, showCart: !state.showCart };
		case "ADD_GOOD":
			return {
				...state,
				preparedGood: [...state.preparedGood, action.payload]
			};
		case "REMOVE_GOOD":
			let checkForOneElement = false;

			return {
				...state,
				preparedGood: state.preparedGood
					.map(good =>
						// проверка айди, айди совпадают и чек равен ложь
						good.id === action.payload && !checkForOneElement
							? ((checkForOneElement = true), false)
							: good
					)
					.filter(_good => _good)
			};
		default:
			return state;
	}
}

export default cartReducer;
