const filterState = {
	brand: [],
	size: [],
	year: [],
	rangeValues: [400, 9999],
	category: ""
};

function filterReducer(state = filterState, action) {
	switch (action.type) {
		case "FILTER_BY_KEY":
			return {
				...state,
				[action.payload.key]: [...action.payload.value]
			};
		case "CLIER_FILTER":
			return {
				...state,
				brand: [],
				size: [],
				year: [],
				rangeValues: [400, 9999]
			};
		case "CHANGE_CATEGORI":
			return { ...state, category: action.payload };
		default:
			return state;
	}
}

export default filterReducer;
