import React from "react";
import Goods from "../components/Goods/";
import { connect } from "react-redux";
import Filter from "../components/Filter/";

const s = {
  PageFilter: {
    display: "flex"
  }
};

class _Filter extends React.Component {
  state = {};

  render() {
    const { history } = this.props;

    return (
      <div style={s.PageFilter}>
        <Filter />
        <Goods history={history} />
      </div>
    );
  }
}
const a = state => ({
  userDate: state.user
});
const b = dispatch => {
  return {
    updateUserDate: payload => dispatch({ type: "UPDATE_USER", payload })
  };
};

export default connect(
  a,
  b
)(_Filter);
