import React from "react";
import { connect } from "react-redux";
import { Card } from "primereact/card";
import { Button } from "primereact/button";
import { Link } from "react-router-dom";
import localForage from "localforage";
import s from "./Filter.module.sass";
import { Checkbox } from "primereact/checkbox";
import { Slider } from "primereact/slider";

class Filter extends React.Component {
  state = {
    brand: [],
    size: [],
    year: [],
    rangeValues: [400, 9999]
  };

  coponentDidMount() {
    const { chengeCategori } = this.props;
    const category = window.location.pathname.split("/")[2];
    chengeCategori(category);
  }

  onChangeCheckbox = type => value => event => {
    const findetType = this.state[type];
    const filterByKey = this.props.filterByKey;
    let result = [];
    if (findetType.includes(value)) {
      result = findetType.filter(_brand => _brand !== value);
    } else {
      result = [...findetType, value];
    }
    this.setState({ [type]: result });
    filterByKey({ key: type, value: result });
  };

  onChangeRengeSlider = type => event => {
    const filterByKey = this.props.filterByKey;
    const rv = this.state.rangeValues;
    rv[type] = Number(event.target.value);
    filterByKey({ key: "rangeValues", value: rv });

    this.setState({ rangeValues: rv });
    /*    const findetType = this.state[type];*/
  };

  render() {
    const { goodsList = [], rangeValues } = this.props;
    const brand = [];
    const size = [];
    const year = [];

    goodsList.forEach(good => {
      if (!brand.includes(good.brand)) brand.push(good.brand);
      (good.size || []).forEach(_size => {
        if (!size.includes(_size)) {
          size.push(_size);
        }
      });
      if (!year.includes(good.year)) year.push(good.year);
    });

    return (
      <div className={s.Filter}>
        <h3>Brand</h3>
        <div className={s.Brand}>
          {brand.map((brand, key) => {
            return (
              <div key={key}>
                <Checkbox
                  onChange={this.onChangeCheckbox("brand")(brand)}
                  checked={this.state.brand.includes(brand)}
                ></Checkbox>{" "}
                {brand}
              </div>
            );
          })}
        </div>
        <h3>Size</h3>
        <div className={s.Size}>
          {" "}
          {size.map((size, key) => {
            return (
              <div key={key}>
                <Checkbox
                  onChange={this.onChangeCheckbox("size")(size)}
                  checked={this.state.size.includes(size)}
                ></Checkbox>{" "}
                {size}
              </div>
            );
          })}
        </div>
        <h3>year</h3>
        <div className={s.year}>
          {year.map((year, key) => {
            return (
              <div key={key}>
                <Checkbox
                  onChange={this.onChangeCheckbox("year")(year)}
                  checked={this.state.year.includes(year)}
                ></Checkbox>{" "}
                {year}
              </div>
            );
          })}
        </div>
        <h3>Price</h3>
        <div className={s.Price}>
          <input
            onChange={this.onChangeRengeSlider(0)}
            key={"min"}
            style={{ width: "80px" }}
            value={this.state.rangeValues[0]}
          />
          <Slider
            style={{ width: "200px" }}
            min={400}
            max={9999}
            value={this.state.rangeValues}
            onChange={e => this.setState({ rangeValues: e.value })}
            onSlideEnd={e => {
              this.onChangeRengeSlider(0)({ target: { value: e.values[0] } });
              this.onChangeRengeSlider(1)({ target: { value: e.values[1] } });
            }}
            range={true}
          />
          <input
            onChange={this.onChangeRengeSlider(1)}
            key={"max"}
            style={{ width: "80px", marginLeft: "120px" }}
            value={this.state.rangeValues[1]}
          />
        </div>
      </div>
    );
  }
}

const a = store => ({
  goodsList: store.goods.goodsList,
  cartItems: store.cart.preparedGood,
  brand: store.filter.brand,
  size: store.filter.size,
  year: store.filter.year,
  rangeValues: store.filter.rangeValues
});
const b = dispatch => {
  return {
    addGood: _good => dispatch({ type: "ADD_GOOD", payload: _good }),
    removeGood: _id => dispatch({ type: "REMOVE_GOOD", payload: _id }),
    chengeCategori: category =>
      dispatch({ type: "CHANGE_CATEGORI", payload: category }),
    filterByKey: payload => dispatch({ type: "FILTER_BY_KEY", payload })
  };
};

export default connect(
  a,
  b
)(Filter);
